<template>
  <div>
    <CCard>
      <CCardBody>
        <h4 style="color:#8c5c7d"><CIcon name="cil-people" size="lg"/> 帳號管理</h4>
        <hr class="mt-0 mb-2">
        <!-- <CNavbar
          expandable="md"
          style="background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);border-radius: 6px;"
        >
          <CNavbarBrand><h3>帳號管理</h3></CNavbarBrand>
          <CNavbarNav class="ml-auto">
            <CNavItem><router-link :to="{ name: 'Home' }">Home</router-link></CNavItem>
            <CNavItem><router-link :to="{ name: 'Login' }">登出</router-link></CNavItem>
          </CNavbarNav>
        </CNavbar> -->
        <CRow class="pt-2">
          <CCol md="3">
            <CButton variant="outline" color="info" class="mr-2 mb-4" @click="openSearchModal"><font-awesome-icon icon="search"/> 查詢</CButton>
            <CButton variant="outline" color="info" class="mr-2 mb-4" @click="openAddModal"><font-awesome-icon icon="user-plus"/> 新增</CButton>
          </CCol>
        </CRow>
        <!-- searchModal Component start -->
        <CModal
          :show.sync="searchModal"
          :no-close-on-backdrop="true"
          title="查詢帳號"
          color="info"
        >
          <CForm>
            <div class="form-group row">
              <label class="col-sm-3 col-form-label text-center">帳號</label>
              <div class="col-sm-8">
                <multiselect v-model="searchObj.account" :options="optionsAccount" @open="openOptionsAccount" selectLabel="選取" deselectLabel="取消" placeholder="輸入幾碼搜尋"></multiselect>
              </div>
            </div>
            <div class="form-group row">
              <label class="col-sm-3 col-form-label text-center">姓名</label>
              <div class="col-sm-8">
                <multiselect v-model="searchObj.name" :options="optionsName" @open="openOptionsName" selectLabel="選取" deselectLabel="取消" placeholder="輸入幾碼搜尋"></multiselect>
              </div>
            </div>
            <div class="form-group row">
              <label class="col-sm-3 col-form-label text-center">工號</label>
              <div class="col-sm-8">
                <multiselect v-model="searchObj.jobNum" :options="optionsJobNum" @open="openOptionsJobNum" selectLabel="選取" deselectLabel="取消" placeholder="輸入幾碼搜尋"></multiselect>
              </div>
            </div>
            <div class="form-group row">
              <label class="col-sm-3 col-form-label text-center">部門</label>
              <div class="col-sm-8">
                <multiselect v-model="searchObj.dept" :options="optionsDept" @open="openOptionsDept" selectLabel="選取" deselectLabel="取消" placeholder="輸入幾碼搜尋"></multiselect>
              </div>
            </div>
            <div class="form-group row">
              <label class="col-sm-3 col-form-label text-center">權限</label>
              <div class="col-sm-8">
                <multiselect v-model="searchObj.role" :options="optionsRole" @open="openOptionsRole" selectLabel="選取" deselectLabel="取消" placeholder="輸入幾碼搜尋"></multiselect>
              </div>
            </div>
          </CForm>
          <template #footer>
            <CButton @click="searchModal = false" color="secondary">取消</CButton>
            <CButton @click="Search" color="info">查詢</CButton>
          </template>
        </CModal>
        <!-- searchModal Component end -->
        <!-- addModal Component start -->
        <CModal
          :show.sync="addModal"
          :no-close-on-backdrop="true"
          title="新增帳號"
          color="info"
        >
          <CForm class="px-5 py-2">
            <div v-if="bExist" class="small text-danger float-right">此帳號已存在!</div>
            <CInput v-model="addObj.account" placeholder="帳號" autocomplete="username" @input="checkAccount">
              <template #prepend-content><CIcon name="cil-user"/></template>
            </CInput>
            
            <CInput v-model="addObj.name" placeholder="姓名" autocomplete="username">
              <template #prepend-content><CIcon name="cil-user"/></template>
            </CInput>
            <CInput v-model="addObj.jobNum" placeholder="工號" autocomplete="username" >
              <template #prepend-content><CIcon name="cil-user"/></template>
            </CInput>
            <div>
              <multiselect v-model="addObj.dept" :options="optionsDept" @open="openOptionsDept" selectLabel="選取" deselectLabel="取消" placeholder="部門"></multiselect>
            </div>
            <div class="py-2">
              <multiselect v-model="addObj.role" :options="optionsRole" @open="openOptionsRole" selectLabel="選取" deselectLabel="取消" placeholder="權限"></multiselect>
            </div>
            <CInput v-model="addObj.password" placeholder="密碼" type="password" autocomplete="new-password" >
              <template #prepend-content><CIcon name="cil-lock-locked"/></template>
            </CInput>
            <div v-if="bDiff" class="small text-danger float-right">確認密碼與密碼不符!</div>
            <CInput v-model="addObj.confirmPassword" @input="checkPassword" placeholder="確認密碼" type="password" autocomplete="new-password" class="mb-4">
              <template #prepend-content><CIcon name="cil-lock-locked"/></template>
            </CInput>
          </CForm>
          <template #footer>
            <CButton @click="addModal = false" color="secondary">取消</CButton>
            <CButton @click="Add" color="info">新增</CButton>
          </template>
        </CModal>
        <!-- addModal Component end -->
        <!-- modifyModal Component start -->
        <CModal
          :show.sync="modifyModal"
          :no-close-on-backdrop="true"
          title="修改基本資料"
          color="info"
        >
          <CForm class="px-5 py-2">
            <CInput v-model="modifyObj.account" disabled placeholder="帳號" autocomplete="username" @input="checkAccount">
              <template #prepend-content><CIcon name="cil-user"/></template>
            </CInput>
            
            <CInput v-model="modifyObj.name" placeholder="姓名" autocomplete="username">
              <template #prepend-content><CIcon name="cil-user"/></template>
            </CInput>
            <CInput v-model="modifyObj.jobNum" placeholder="工號" autocomplete="username" >
              <template #prepend-content><CIcon name="cil-user"/></template>
            </CInput>
            <div>
              <multiselect v-model="modifyObj.dept" :options="optionsDept" @open="openOptionsDept" selectLabel="選取" deselectLabel="取消" placeholder="部門"></multiselect>
            </div>
            <div class="py-2">
              <multiselect v-model="modifyObj.role" :options="optionsRole" @open="openOptionsRole" selectLabel="選取" deselectLabel="取消" placeholder="權限"></multiselect>
            </div>
            <!-- <CInput v-model="modifyObj.password" placeholder="密碼" type="password" autocomplete="new-password" >
              <template #prepend-content><CIcon name="cil-lock-locked"/></template>
            </CInput>
            <div v-if="bDiff" class="small text-danger float-right">確認密碼與密碼不符!</div>
            <CInput v-model="modifyObj.confirmPassword" @input="checkPassword" placeholder="確認密碼" type="password" autocomplete="new-password" class="mb-4">
              <template #prepend-content><CIcon name="cil-lock-locked"/></template>
            </CInput> -->
          </CForm>
          <template #footer>
            <CButton @click="modifyModal = false" color="secondary">取消</CButton>
            <CButton @click="Modify" color="info">存檔</CButton>
          </template>
        </CModal>
        <!-- modifyModal Component end -->
        <!-- modifyPsModal Component start -->
        <CModal
          :show.sync="modifyPsModal"
          :no-close-on-backdrop="true"
          title="重設密碼"
          color="info"
        >
          <CForm class="px-5 py-2">
            <CInput v-model="modifyObj.account" disabled placeholder="帳號" autocomplete="username" @input="checkAccount">
              <template #prepend-content><CIcon name="cil-user"/></template>
            </CInput>
            <CInput v-model="modifyObj.password" placeholder="新密碼" type="password" autocomplete="new-password" >
              <template #prepend-content><CIcon name="cil-lock-locked"/></template>
            </CInput>
            <div v-if="bDiff" class="small text-danger float-right">確認密碼與密碼不符!</div>
            <CInput v-model="modifyObj.confirmPassword" @input="checkNewPassword" placeholder="確認新密碼" type="password" autocomplete="new-password" class="mb-4">
              <template #prepend-content><CIcon name="cil-lock-locked"/></template>
            </CInput>
          </CForm>
          <template #footer>
            <CButton @click="modifyPsModal = false" color="secondary">取消</CButton>
            <CButton @click="ModifyPassword" color="info">存檔</CButton>
          </template>
        </CModal>
        <!-- modifyPsModal Component end -->
        
        <h6 class="float-right">資料筆數: <CBadge color="warning">{{items.length}}</CBadge></h6>
        <CDataTable
          :hover="hover"
          :striped="striped"
          :bordered="bordered"
          :small="small"
          :fixed="fixed"
          :items="items"
          :fields="fields"
          :items-per-page="small ? 20 : 50"
          :dark="dark"
          class="text-center"
          border
          pagination
        >
          <template #no-items-view>
            <br>
            <h5>
              無資料
            </h5>
            <br>
          </template>
          <template #start_time="{item}">
            <td>
              {{formatTime(item.start_time)}}
            </td>
          </template>
          <template #action="{item}">
            <td class="p-1">
              <CButton type="reset" class="mx-1 my-1" variant="outline" color="info" @click="openModifyModal(item)"><font-awesome-icon icon="pencil-alt"/> 修改基本資料</CButton>
              <CButton type="reset" class="mx-1 my-1" variant="outline" color="info" @click="openModifyPsModal(item)"><font-awesome-icon icon="key"/> 重設密碼</CButton>
              <CButton type="reset" class="mx-1 my-1" color="danger" @click="Delete(item)"><font-awesome-icon icon="trash-alt"/> 刪除</CButton>
            </td>
          </template>
        </CDataTable>
      </CCardBody>
    </CCard>
  </div>
</template>

<script>
import usersData from './users/UsersData'
export default {
  name: 'Users',
  data: () => {
    return {
      searchModal: false,
      addModal: false,
      modifyModal: false,
      modifyPsModal: false,
      searchObj: {
        account: '',
        name: '',
        jobNum: '',
        dept: '',
        role: ''
      },
      addObj: {
        account: '',
        name: '',
        jobNum: '',
        dept: '',
        role: '',
        password: '',
        confirmPassword: ''
      },
      modifyObj: {
        account: '',
        name: '',
        jobNum: '',
        dept: '',
        role: '',
        password: '',
        confirmPassword: ''
      },
      optionsAccount: [],
      optionsName: [],
      optionsJobNum: [],
      optionsDept: [],
      optionsRole: [],
      items: [],
      fields: [
        { key: 'account', label: '帳號', _style: 'background:#FFFFF1' },
        { key: 'name', label: '姓名', _style: 'background:#FFFFF1' },
        { key: 'job_number', label: '工號', _style: 'background:#FFFFF1' },
        { key: 'dept', label: '部門', _style: 'background:#FFFFF1' },
        { key: 'role', label: '權限', _style: 'background:#FFFFF1' },
        { key: 'action', label: '操作', _style: 'background:#FFFFF1' }
      ],
      hover: true,
      striped: true,
      bordered: true,
      small: false,
      fixed: true,
      dark: false,
      bExist: false,   // 帳號是否已存在
      bDiff: false,    // 確認密碼是否與密碼相同
    }
  },
  paginationProps: {
    align: 'center',
    doubleArrows: false,
    previousButtonHtml: 'prev',
    nextButtonHtml: 'next'
  },
  created() {
    this.$http.get('/api/accounts')
    .then(res => {
      this.items = res.data
      this.optionsAccount = []
      for(let i=0; i<res.data.length; i++) {
        this.optionsAccount.push(res.data[i].account)
      }
    })
    .catch(err => { console.log(err.response.data.message) })
  },
  methods: {
    openSearchModal() {
      this.searchObj.account = null
      this.searchObj.name = null
      this.searchObj.jobNum = null
      this.searchObj.dept = null
      this.searchObj.role = null
      this.searchModal = true
    },
    openAddModal() {
      this.addModal = true
    },
    openModifyModal(item) {
      this.modifyObj.account = item.account
      this.modifyObj.name = item.name
      this.modifyObj.jobNum = item.job_number
      this.modifyObj.dept = item.dept
      this.modifyObj.role = item.role
      this.modifyModal = true
    },
    openModifyPsModal(item) {
      this.modifyObj.account = item.account
      this.modifyObj.password = ''
      this.modifyObj.confirmPassword = ''
      this.modifyPsModal = true
    },
    openOptionsAccount() {
      this.optionsAccount = []
      this.$http.get('/api/accounts')
      .then(res => {
        for(let i=0; i<res.data.length; i++) {
          this.optionsAccount.push(res.data[i].account)
        }
      })
      .catch(err => { console.log(err.response.data.message) })
    },
    openOptionsName() {
      this.optionsName = []
      this.$http.get('/api/accounts')
      .then(res => {
        for(let i=0; i<res.data.length; i++) {
          this.optionsName.push(res.data[i].name)
        }
      })
      .catch(err => { console.log(err.response.data.message) })
    },
    openOptionsJobNum() {
      this.optionsJobNum = []
      this.$http.get('/api/accounts')
      .then(res => {
        for(let i=0; i<res.data.length; i++) {
          this.optionsJobNum.push(res.data[i].job_number)
        }
      })
      .catch(err => { console.log(err.response.data.message) })
    },
    openOptionsDept() {
      this.optionsDept = []
      this.$http.get('/api/accounts')
      .then(res => {
        for(let i=0; i<res.data.length; i++) {
          this.optionsDept.push(res.data[i].dept)
        }
      })
      .catch(err => { console.log(err.response.data.message) })
    },
    openOptionsRole() {
      this.optionsRole = []
      this.$http.get('/api/accounts')
      .then(res => {
        for(let i=0; i<res.data.length; i++) {
          this.optionsRole.push(res.data[i].role)
        }
      })
      .catch(err => { console.log(err.response.data.message) })
    },
    Search() {
      let param = {
        account: this.searchObj.account,
        name: this.searchObj.name,
        job_number: this.searchObj.jobNum,
        dept: this.searchObj.dept,
        role: this.searchObj.role
      }

      this.$http.get('/api/accounts/find', {params: param})
      .then(res => {
        this.items = res.data
      })
      .catch(err => { console.log(err.response.data.message) })

      this.searchModal = false
    },
    Add() {
      if(this.bExist || !this.addObj.account) {
        alert('請確認輸入之帳號是否正確!')
        return
      }
      if(this.bDiff || !this.addObj.password) {
        alert('請確認輸入之密碼是否正確')
        return
      }

      let user = {
        account: this.addObj.account,
        name: this.addObj.name,
        job_number: this.addObj.jobNum,
        dept: this.addObj.dept,
        role: this.addObj.role,
        password: this.addObj.password
      }

      this.$http.post('/api/accounts', {data: user})
      .then(res => {
        this.items.splice(0, 0, user)
      })
      .catch(err => { console.log(err.response.data.message) })

      this.addModal = false
    },
    checkAccount() {
      var user = { account: this.addObj.account }
      this.$http.post('/api/accounts/checkAccount', { data: user })
      .then(res => {
        if(res.data.length>0) {  // 帳號存在
          this.bExist = true
        } else {
          this.bExist = false
        }
      })
      .catch(err => { console.log(err.response.data.message) })
    },
    checkPassword() {
      let str1 = String(this.addObj.password)
      let str2 = String(this.addObj.confirmPassword)
      let arr1 = str1.split('')
      let arr2 = str2.split('')
      if(arr2.length>arr1.length) {
        this.bDiff = true
      } else {
        for(let i=0; i<arr2.length; i++) {
          if(arr2[i]!==arr1[i]) {
            this.bDiff = true
          } else {
            this.bDiff = false
          }
        }
      }
    },
    checkNewPassword() {
      let str1 = String(this.modifyObj.password)
      let str2 = String(this.modifyObj.confirmPassword)
      let arr1 = str1.split('')
      let arr2 = str2.split('')
      if(arr2.length>arr1.length) {
        this.bDiff = true
      } else {
        for(let i=0; i<arr2.length; i++) {
          if(arr2[i]!==arr1[i]) {
            this.bDiff = true
          } else {
            this.bDiff = false
          }
        }
      }
    },
    Delete(item) {
      if(confirm('確定要將帳號:'+item.account+'，這筆資料刪除?')){
        let obj = {account: item.account}
        this.$http.delete(`/api/accounts/${item.account}`)
        .then(res => {
          // 刪除表格中該筆資料
          let idx = this.items.findIndex(x => x.account===item.account)
          this.items.splice(idx, 1)
        })
        .catch(err => { console.log(err.response.data.message) })
      }
    },
    Modify() {
      let user = {
        // account: this.modifyObj.account,
        name: this.modifyObj.name,
        job_number: this.modifyObj.jobNum,
        dept: this.modifyObj.dept,
        role: this.modifyObj.role
      }

      this.$http.put(`/api/accounts/${this.modifyObj.account}`, {data: user})
      .then(res => {
        // update table data
        let idx = this.items.findIndex(x => x.account===this.modifyObj.account)
        this.items[idx].name = this.modifyObj.name
        this.items[idx].job_number = this.modifyObj.jobNum
        this.items[idx].dept = this.modifyObj.dept
        this.items[idx].role = this.modifyObj.role
      })
      .catch(err => { console.log(err.response.data.message) })

      this.modifyModal = false
    },
    ModifyPassword() {
      let user = {
        account: this.modifyObj.account,
        password: this.modifyObj.password
      }

      this.$http.post('/api/accounts/updatePwd', {data: user})
      .then(res => {
        alert("新密碼設定完成")
      })
      .catch(err => { console.log(err.response.data.message) })
      this.modifyPsModal = false
    }
  }
}
</script>
